<template>
    <div class="success container col-6 text-center mt-5">
         <div class="card ">
            <div class="card-header bg-warning text-white"><h3></h3></div>
            <div class="card-body">
                <h5 class="card-title text-warning">,88~~~</h5>
                <h5 class="card-text text-success">{{time}}<button class="btn btn-success" @click.prevent='jump'></button></h5>
            </div>
            <div class="card-footer text-muted"></div>
        </div>
    </div>
</template>

<script>
    export default {
        name:'Success',
        data(){
            return{
                time:5
            }
        },
        methods:{
            jump(){
                this.$router.push('/login')
            },
            countdown(){
                var timer=setInterval(()=>{
                    if(this.time==0){
                        this.$router.push('/login')
                        clearInterval(timer)
                    }else{
                        this.time--
                    }
                },1000)
            }
        },
        mounted(){
            this.countdown()
        }
    }
</script>

<style scoped>

</style>


