<template>
   <div class='container col-6'>
        <div class="text-center">
            <img src="../assets/logo.png" alt="">
        </div>
        <h1 class="text-center text-warning mb-3"></h1>
        <div class="clearfix" style="height:40px;">
            <p class="text-danger col-10 float-right " v-show="msg">{{msg}}</p>
        </div>
        <form>
            <div class="form-group row">
                <label for="username" class="col-2 col-form-label text-right">/:</label>
                <div class="col-10">
                  <input v-model="username" type="text" class="form-control" id="username"  placeholder="">
                </div>
            </div>
            <div class="form-group row">
                <label for="pass" class="col-2 col-form-label text-center">:</label>
                <div class="col-10">
                  <input v-model="password" type="password" class="form-control" id="pass" placeholder="">
                </div>
            </div>
            <div class="form-group row">
                <label for="pass1" class="col-2 col-form-label text-right">:</label>
                 <div class="col-10">
                  <input v-model="repassword" type="password" class="form-control" id="pass1" placeholder="">
                 </div>
            </div>
            <button type="submit" class="btn btn-primary btn-block" @click.prevent='onSubmit'></button>
        </form>
    </div>
</template>

<script>
// axios
import axios from 'axios'
    export default {
        name:'Register',
        data(){
            return {
                username:'',
                password:'',
                repassword:'',
                msg:''
            }
        },
        methods:{
            onSubmit(){
                //
                // let reg1 =/^\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*$/
                let reg1 = /^((1([38]\d|4[57]|5[0-35-9]|7[06-8])\d{8})|([a-zA-Z0-9]{5,})|(([a-z0-9_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})))$/;
                let reg2 = /^(?![a-zA-z]+$)(?!\d+$)(?![!@#$%^&*.,]+$)([a-zA-Z\d]|[a-zA-z!@#$%^&*.,]|[\d!@#$%^&*.,]){6,16}$/
                if(this.username==''){
                    this.msg='*'
                }else if(!reg1.test(this.username)){
                    this.msg='*'
                }else if(this.password==''){
                    this.msg='*'
                }else if(this.password.length<6){
                    this.msg='*'
                }else if(!reg2.test(this.password)){
                    this.msg='*'
                }else if(this.repassword==''){
                    this.msg='*'
                }else if(this.password!=this.repassword){
                    this.msg='*'
                }else{
                    //
                    axios.get('./lh-user.json')
                    .then((res)=>{
                        // console.log(res.data)
                        // res.datanull
                        // console.log( res.data)  
                        // res.dataobjectarray
                        const result = []
                        for(let key in res.data){
                        result.push(res.data[key])  
                        } 
                        // console.log(result)
                        const trueUser = result.filter((item)=>{
                            return item.username===this.username 
                        })
                        if(trueUser.length>0){
                        alert('') 
                        }else{
                        // 
                        const formData={
                            username:this.username,
                            password:this.password 
                        }
                        axios.post('./lh-user.json',formData)
                        .then(res=>{
                            // alert('') 
                            // console.log(res.data)
                            //
                            this.$router.push('/Success')
                            // this.$router.push('/login')
                        })  
                        }
                    })
                }
                //3s
                 if(this.msg!==''){
                    setTimeout(()=>{
                        this.msg=''
                    },3000)
                }
            }
        }
    }
</script>

<style scoped>

</style>


