<template>
    <div class="order mt-5 text-center">
        <h2 class="text-center text-warning"></h2>
        <template v-if="newMenu.length>0">
        <table class="table text-center">
            <thead>
                <tr class="bg-primary text-white">
                    <th ></th>
                    <th >()</th>
                    <th ></th>
                </tr>
            </thead>
            <tbody v-for="(item,index) in newMenu" :key='index'>
                <tr>
                    <td colspan="3" class="text-success"><h4>{{item.name}}</h4></td>
                </tr>
                <tr>
                    <td colspan="3" class="text-info">{{item.desc}}</td>
                </tr>
                <tr v-for="(item1,index1) in item.options" :key='index1'>
                    <td>{{item1.size}}</td>
                    <td>{{item1.price}}</td>
                    <td><button class="btn btn1 btn-success text-white" data-toggle="tooltip" data-placement="top" title="10" @click.prevent="addtoCar(item,item1)">+</button></td>
                </tr>
            </tbody>
        </table>
        </template>
         <template v-else>
                <img src="../../../static/imgs/tip.png" title="">
                <h3 class="text-success">~~~</h3>
                <p class="mt-3 text-warning">PIZZA()"</p>
        </template>
    </div>
</template>

<script>
    export default {
        name:'Order',
        props:['newMenu'],
        methods:{
            addtoCar(item,item1){
                this.$emit('ordering',{item:item,item1:item1})
            }
          
        },
        updated(){
            //
            $('.btn1').tooltip()
        }
    }
</script>

<style scoped>

</style>


