<template>
  <div id="app">
    <!--  -->
  <Header></Header>
  <!--  -->
  <router-view></router-view>
    <div class="container to-center text-center">
      <!--  -->
      <router-view name="delivery"></router-view>
       <!--  -->
        <div class="btn btn-outline-primary gotop" id="goTop" @click="goTop"></div>
    </div>
  </div>
</template>

<script>
//header
import Header from './components/Header'
export default {
  name: 'App',
  data(){
    return {
      title:''
    }
  },
  components:{
    Header,
  },
  methods:{
    goTop(){
      document.documentElement.scrollTop=0
    },
  }
}
window.onscroll=function(){
  var oDiv=document.getElementById('goTop')
  
  var scrollTop = window.scrollTop ||document.documentElement.scrollTop
  //  console.log(scrollTop)
  if(scrollTop>200){
      oDiv.style.display='block'
  }else{
      oDiv.style.display='none'
  }
}
</script>

<style>
  .to-center{
      padding-left: 70px;
  }
  /*  */
  .gotop{
      position: fixed;
      right: 50px;
      bottom: 70px;
      font-size: 30px;
  }
</style>



